body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body{
    background-image: url('/images/fond.jpeg');
}

.login {
    background-color: black;
    width: 70%;
    height: 100vh;
    margin: auto
}

.loginInput {
    width : 60%;
    height: 20px;
    padding: 6px;
    margin: 20px;
    border-radius: 20px;
}

button {
    width: 30%;
    height: 40px;
    padding: 6px;
    background-color: #F8A907;
    font-weight: bold;
    font-size: 16px;
    margin-top: 20px;
    border-radius: 20px;
}

@media screen and (max-width: 640px) {
    .login {
        background-color: black;
        width: 100%;
        height: 100vh;}
        
    button{
        border-radius: 20px;
        width: 60%
    }
}




.column {
  border-radius: 10px;
  background-color: #7f8c8d;
  height: auto;
  padding: 10px;
  min-width: 270px;
  color: white;
  font-size: 25px;
}

.App {
  text-align: center;

}





body {
  background-image: url("/images/fond.jpeg");
}

.wrapper {
  display: flex;
  justify-content: center;
  height: calc(100vh - 63px);
}

.register {
  background-color: black;
  width: 60%;
  height: auto;
  margin: auto;
  padding: 100px;
}

input {
  width: 70%;
  height: 20px;
  padding: 6px;
  margin: 20px;
}

button {
  width: 40%;
  height: 40px;
  padding: 6px;
  background-color: #f8a907;
  font-weight: bold;
  font-size: 16px;
  margin: 20px auto;
}

.reg {
  color: #f8a907;
  margin: auto;
}

@media screen and (max-width: 640px) {
  .wrapper {
    width: 100%;
    height: 100vh;
  }
  .register {
    background-color: black;
    width: 100%;
    height: 100vh;
    margin: 0;
    padding: 0;
  }
  input {
    width: 80%;
  }
  button {
    width: 70%;
  }
}

body {
  background-image: url("/images/fond.jpeg");
}

.wrapper {
  display: flex;
  justify-content: center;
  height: calc(100vh - 63px);
}

.register {
  background-color: black;
  width: 60%;
  height: auto;
  margin: auto;
  padding: 100px;
}

input {
  width: 70%;
  height: 20px;
  padding: 6px;
  margin: 20px;
}

textarea {
  width: 70%;
  height: 20px;
  padding: 6px;
  margin: 20px;
  min-height: 200px;
}

button {
  width: 40%;
  height: 40px;
  padding: 6px;
  background-color: #f8a907;
  font-weight: bold;
  font-size: 16px;
  margin: 20px auto;
  outline: none;
}

.reg {
  color: #f8a907;
  margin: auto;
}

@media screen and (max-width: 640px) {
  .wrapper {
    width: 100%;
    height: 100vh;
  }
  .register {
    background-color: black;
    width: 100%;
    height: 100vh;
    margin: 0;
    padding: 0;
  }
  input,
  textarea {
    width: 80%;
  }
  button {
    width: 70%;
  }
}

